<template>
  <div class="search">
    <van-nav-bar left-text="返回" left-arrow fixed @click-left="back">
      <template #right>
        <div class="home-search">
          <van-search
            v-model="name"
            @search="search"
            ref="search"
            placeholder="输入商品关键字"
            shape="round"
            :clearable="false"
          />
        </div>
      </template>
    </van-nav-bar>
    <div class="listSearch">
      <div class="product-list clearfix">
        <div
          class="fl product-img-box"
          v-for="(item, index) in searchData"
          :key="index"
          @click="GoDetail(item.goods_id)"
        >
          <img class="auto-img" :src="item.goods_big_logo" />
          <div class="product-text-box">
            <div class="product-title-box clearfix">
              <div class="product-Special-price fl" v-if="item.isDiscount">特卖价</div>
              <div class="product-price fl">￥{{ item.goods_price }}</div>
              <div class="product-discount fl" v-if="item.isDiscount">
                {{ item.discountPrice }}折
              </div>
            </div>
            <div class="product-name tow-text">
              {{ item.goods_name }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "../assets/less/search.less";
import "../assets/less/listsearch.less";
export default {
  name: "Search",
  data() {
    return {
      //搜索商品关键字
      name: "",
      //搜索商品数据
      searchData: [],
      myquery: "",
    };
  },
  created() {
    this.saveProductData();
    this.myquery = this.$route.params.query;
    this.getMyqueryData();
  },
  beforeRouteLeave (to, from, next) {
    localStorage.removeItem('saveName')
    next();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    // 搜索
    search() {
      if (this.name == 0) {
        return this.$toast("输入内容不能为空");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/search",
        params: {
          query: this.name,
        },
      })
        .then((result) => {
          localStorage.setItem("saveName", this.name);
          let data = result.data.message.goods;
          let data2 = result.data.message.goods.filter((item) => {
            if (item.goods_big_logo.length > 1) {
              return item;
            }
          });
          data.map((v, i) => {
            if (v.goods_big_logo === "") {
              v.goods_big_logo = data2[data2.length - 1].goods_big_logo;
              v.goods_name = data2[[data2.length - 1]].goods_name;
            }
            if (v.goods_price == 0) {
              v.goods_price = 1888;
            }
            v.isDiscount = false;
            if (v.goods_price % 2 != 0) {
              v.isDiscount = true;
              v.discountPrice = `${Math.ceil(Math.random() * 9)}.${Math.ceil(
                Math.random() * 9
              )}`;
            }
          });
          this.searchData = result.data.message.goods;
          if (this.searchData.length == 0) {
            this.$toast("哎呀~找不到该商品");
          }
          setTimeout(() => {
            this.$toast.clear();
          }, 300);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 保存商品信息
    saveProductData() {
      let saveName = localStorage.getItem("saveName");
      if (saveName == null || saveName ==='') {
        return;
      }
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/search",
        params: {
          query: saveName,
        },
      })
        .then((result) => {
          let data = result.data.message.goods;
          let data2 = result.data.message.goods.filter((item) => {
            if (item.goods_big_logo.length > 1) {
              return item;
            }
          });
          data.map((v, i) => {
            if (v.goods_big_logo === "") {
              v.goods_big_logo = data2[data2.length - 1].goods_big_logo;
              v.goods_name = data2[[data2.length - 1]].goods_name;
            }
            if (v.goods_price == 0) {
              v.goods_price = 1888;
            }
            v.isDiscount = false;
            if (v.goods_price % 2 != 0) {
              v.isDiscount = true;
              v.discountPrice = `${Math.ceil(Math.random() * 9)}.${Math.ceil(
                Math.random() * 9
              )}`;
            }
          });
          this.searchData = result.data.message.goods;
          if (this.searchData.length == 0) {
            this.$toast("哎呀~找不到该商品");
          }
          setTimeout(() => {
            this.$toast.clear();
          }, 300);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 获取myquery数据
    getMyqueryData() {
      if(!this.myquery){
        return
      }
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/search",
        params: {
          query: this.myquery,
        },
      })
        .then((result) => {
          let data = result.data.message.goods;
          let data2 = result.data.message.goods.filter((item) => {
            if (item.goods_big_logo.length > 1) {
              return item;
            }
          });
          data.map((v, i) => {
            if (v.goods_big_logo === "") {
              v.goods_big_logo = data2[data2.length - 1].goods_big_logo;
              v.goods_name = data2[[data2.length - 1]].goods_name;
            }
            if (v.goods_price == 0) {
              v.goods_price = 1888;
            }
            v.isDiscount = false;
            if (v.goods_price % 2 != 0) {
              v.isDiscount = true;
              v.discountPrice = `${Math.ceil(Math.random() * 9)}.${Math.ceil(
                Math.random() * 9
              )}`;
            }
            this.searchData = result.data.message.goods;
          });
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 跳转商品详情
    GoDetail(goods_id) {
      this.$router.push({ name: "Detail", params: { goods_id, saveName: this.name } });
    },
  },
};
</script>

<style lang="less" scoped></style>
